,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,UAAU;AACd;AAIA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,YAAY;IACZ,uBAAuB;AAC3B;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,uBAAuB;IACvB,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;IACf,mBAAmB;AACvB;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,cAAc;AAClB;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,iBAAiB;AACrB;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;AAEA;IACI,eAAe;IACf,oBAAoB;AACxB;AAEA;IACI,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,gBAAgB;AACpB;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,qBAAqB;AACzB;AACJ;AAEA;AACI;QACI,cAAc;QACd,WAAW;AACf;AACJ","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IAuthenticationUseCase } from \"../../../../domain/useCase/IAuthenticationUseCase\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport ApplicationCard from \"../../components/application/appCard.vue\"; // resolvable by vue transpiler (bundled)\nimport AppDataTable from \"../../components/application/appDataTable.vue\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst router = container.resolve<IRouter>(IRouter);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\n\nlet viewData = ref(\"Card\");\nconst selectedOrganizationId = computed(() => viewModel.user!.selectedOrganization?.id);\nconst selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\n\nconst isOwner = (userId: string) => viewModel.user!.selectedOrganization?.ownerUserIds ? viewModel.user!.selectedOrganization.ownerUserIds.includes(userId) : viewModel.user!.selectedOrganization?.creatorUserId === userId;\nconst selectedOrganizationCreaterId = computed(() => viewModel.user!.selectedOrganization?.creatorUserId);\nconst routerParams = router.getRouteParams<{ orgId?: string }>();\n// const organizations = ref(['']);\n//     viewModel.user?.organizations.forEach(org => {\n//     if(org.status != 'deleting') {\n//         organizations.value.push(org.name);\n//     }\n// })\n\nasync function onOrganizationSelect(id?: string) {\n    await executor.execute<void>(async () => {\n        await organization.changeOrganization(id, {});\n    }, { loading: true, noContinueInBgBtn: true });\n}\nfunction respondToInvitation(accept: boolean, invitationId: string, organizationId?: string) {\n    executor.execute(async () => {\n        await organization.respondToInvitation({ accept, invitationId });\n        // await organization.listOrganizations();\n        await authentication.signIn({});\n        if (accept) {\n            if (selectedOrganizationId.value && selectedOrganizationId.value?.length > 0) {\n                await onOrganizationSelect(selectedOrganizationId.value);\n            }\n            else {\n                await onOrganizationSelect(organizationId!);\n            }\n        }\n    }, { loading: true, noContinueInBgBtn: true });\n}\n\nfunction CreateApp(templateName?: string) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Create Application\", height: \"383px\", width: \"466px\" },\n        { templateName }\n    );\n}\n\n\nfunction changeView(view: any) {\n    viewData.value = view;\n    let tableVButton = document.getElementById(\"TableView\");\n    let cardVButton = document.getElementById(\"CardView\");\n    if (viewData.value == \"Table\") {\n        cardVButton?.classList.remove(\"activeView\");\n        tableVButton?.classList.add(\"activeView\");\n    } else if (viewData.value == \"Card\") {\n        tableVButton?.classList.remove(\"activeView\");\n        cardVButton?.classList.add(\"activeView\");\n    }\n}\nexecutor.execute(async () => {\n    // if (!(selectedOrganizationId.value && selectedOrganizationId.value?.length > 0) && viewModel.user!.organizations.length > 0) {\n        await onOrganizationSelect(selectedOrganizationId.value);\n    // }\n}, { loading: false })\n\n</script>\n\n<template>\n    <div>\n        <div v-for=\"invitation in viewModel.user?.invitations\" class=\"invitation-row\" :key=\"invitation.id\">\n            <div class=\"invitation-data\">\n                <img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 16px;\" />\n                <span><b>{{ invitation.inviter.name + ' ' + invitation.inviter.lastName }}</b> invited you to join <b>{{\n                    invitation.organizationName }}</b> organization.</span>\n            </div>\n            <div class=\"invitation-data\">\n                <button class=\"invitation-respond-button\"\n                    @click=\"respondToInvitation(true, invitation.id, invitation.organizationId)\"><i class=\"mdi mdi-check\"\n                        style=\"color:#29C660; font-size: 16px; margin-right: 4px;\"></i>Accept</button>\n                <button class=\"invitation-respond-button\" @click=\"respondToInvitation(false, invitation.id)\"><i\n                        class=\"mdi mdi-close\" style=\"color:#FF6B6B; font-size: 16px; margin-right: 4px;\"></i>Reject</button>\n            </div>\n        </div>\n        <p class=\"app-text\">Create New Application</p>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"createapp-container\">\n            <div class=\"text\"></div>\n            <div class=\"blankapp-logo-card\" @click=\"CreateApp()\">\n                <img src=\"../../../../images/blank-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n            </div>\n            <p class=\"title\">Blank Application</p>\n            <div class=\"blankapp-logo-card\" style=\"left: 211px\" @click=\"CreateApp('newAppTemplate1')\">\n                <img src=\"../../../../images/template-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n            </div>\n            <p class=\"title\" style=\"left: 226px\">Template Application</p>\n        </div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"filter-text-container\">\n            <span class=\"applications-text\">{{ selectedOrganizationName }} Applications</span>\n            <div class=\"card-filters\">\n                <i class=\"mdi mdi-view-dashboard dashboard-icon activeView\" id=\"CardView\" @click=\"changeView('Card')\"></i>\n                <i class=\"mdi mdi-format-list-bulleted bulleted-icon\" id=\"TableView\" @click=\"changeView('Table')\"></i>\n            </div>\n        </div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"cards\">\n            <div v-if=\"viewModel.apps.length == 0\" class=\"empty-org\">\n                <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                    <img style=\" margin: 36px auto 12px;\" src=\"../../../../images/ghost.svg\" />\n                    <span style=\"font-size: 18px; color: #BCBCBC\">It's been empty for a while.</span>\n                </div>\n            </div>\n            <div v-else class=\"row relative-for-children\">\n                <template v-if=\"viewData == 'Card'\">\n                    <div class=\"card-media\" style=\"padding: 0px\" v-for=\"app in viewModel.apps.sort((a, b) =>\n                        a.createDate && b.createDate ? (new Date(a.createDate).getTime() - new Date(b.createDate).getTime() > 0 ? -1 : 1) : 1\n                    )\" :key=\"app.ID\" v-bind:value=\"app.ID\">\n                        <ApplicationCard :app=\"app\" />\n                    </div>\n                </template>\n                <template v-if=\"viewData == 'Table'\">\n                    <AppDataTable />\n                </template>\n            </div>\n        </div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length == 0\">\n            <div class=\"need-organization\">\n                <span>To create an application, you must be in an organization.</span>\n            </div>\n            <span class=\"applications-text\">Your Applications</span>\n            <div class=\"create-org-div\">\n                <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                    <img style=\" margin: 36px auto 12px;\" src=\"../../../../images/ghost.svg\" />\n                    <span style=\"font-size: 22px; color: #BCBCBC\">It's been empty for a while.</span>\n                </div>\n                <!-- <button class=\"create-organization-button-appList\" @click=\"openCreateOrganizationDialog\"><i class=\"mdi mdi-plus\" style=\"font-size: 20px; margin-right: 4px;\"></i>CREATE ORGANIZATION</button> -->\n            </div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n* {\n    font-family: \"Roboto\";\n}\n\n\n.invitation-row {\n    display: flex;\n    justify-content: space-between;\n    margin: 0 -33px;\n    background-color: #f5f5f5;\n    padding: 10px 24px;\n    border-bottom: 1px solid #D5D5D5;\n    align-items: center;\n}\n\n.invitation-data {\n    /* flex-basis: 25%; */\n    /* text-align: center; */\n}\n\n.invitation-label {\n    font-weight: bold;\n    margin-bottom: 5px;\n}\n\n.invitation-value {\n    color: blue;\n}\n\n.relative-for-children {\n    position: relative;\n    margin-left: 0px;\n}\n\n.blankapp-logo {\n    position: absolute;\n    top: calc((100% - 57px) / 2);\n    left: calc((100% - 57px) / 2);\n}\n\n.blankapp-logo-card {\n    width: 163px;\n    height: 119px;\n    border-radius: 4px !important;\n    position: absolute;\n    background: #eeeeee 0% 0% no-repeat padding-box;\n    border: 1px solid #d5d5d5;\n    top: 20px;\n    left: 24px;\n    cursor: pointer;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    background-color: #EEEEEE !important;\n}\n\n.applications-text {\n    font-size: 22px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n}\n\n.app-text {\n    font-size: 18px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n    margin-top: 32px;\n}\n\n.createapp-container {\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n    width: 100%;\n    height: 187px;\n    min-width: 425px;\n    margin-top: 12px;\n}\n\n.title {\n    position: absolute;\n    top: 147px;\n    left: 48px;\n    color: #151233;\n    font-size: 15px;\n}\n\n.filter-text-container {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 35px;\n}\n\n.text {\n    position: absolute;\n    left: 24px;\n    top: 24px;\n}\n\n.dashboard-icon,\n.bulleted-icon {\n    font-size: 30px;\n    margin: 10px;\n    opacity: 0.5;\n    color: #151233;\n}\n\n.card-filters {\n    cursor: pointer;\n    margin-top: -12px;\n}\n\n.cards {\n    margin-top: 10px;\n}\n\n.activeView {\n    opacity: 1;\n}\n\n\n\n.create-organization-button-appList {\n    background: #2C5A86;\n    border-radius: 4px;\n    border: none;\n    padding: 8px 20px;\n    font-size: 17px;\n    font-weight: 500;\n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 48px;\n    justify-content: center;\n}\n\n.need-organization {\n    background: #FFFFFF;\n    color: #BCBCBC;\n    align-items: flex-start;\n    padding: 20px 24px;\n    border-radius: 10px;\n    font-size: 22px;\n    margin-bottom: 32px;\n}\n\n.create-org-div,\n.empty-org {\n    display: grid;\n    align-items: center;\n    justify-content: center;\n}\n\n.invitation-respond-button {\n    background: transparent;\n    border: none;\n    padding: 7px 16px;\n    border-radius: 4px;\n    font-weight: 500;\n    font-size: 14px;\n    color: #151233;\n}\n\n.button-disabled {\n    display: flex;\n    justify-content: center;\n}\n\n.select-organization {\n    width: 260px;\n    font-weight: 500;\n    font-size: 15px;\n    height: 36px;\n    line-height: 36px;\n}\n\n.button-disabled {\n    display: flex;\n    justify-content: center;\n}\n\n.button-disabled button {\n    cursor: default;\n    pointer-events: none;\n}\n\n.button-disabled:hover .premium-account-tooltip {\n    visibility: visible;\n    opacity: 1;\n}\n\n.premium-account-tooltip {\n    visibility: hidden;\n    opacity: 0;\n    margin-top: 36px;\n}\n\n@media (min-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 8);\n    }\n}\n\n@media (max-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 7);\n    }\n}\n\n@media (max-width: 2150px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 6);\n    }\n}\n\n@media (max-width: 1820px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 5);\n    }\n}\n\n@media (max-width: 1550px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 4);\n    }\n}\n\n@media (max-width: 1250px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 3);\n    }\n}\n\n@media (max-width: 950px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 2);\n    }\n}\n\n@media (max-width: 700px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: 100%;\n    }\n}</style>\n"],"sourceRoot":""}]);
